import{bytesToHex}from'@noble/hashes/utils';import{PrivateKey}from'@onekeyfe/kaspa-core-lib';import{tapTweakHash}from'@onekeyhq/core/src/secret/bip340';import ecc from"../../../secret/nobleSecp256k1Wrapper";export function privateKeyFromWIF(wif){return PrivateKey.fromWIF(wif);}export function privateKeyFromBuffer(pri,chainId){return PrivateKey.fromBuffer(pri,chainId);}export function privateKeyFromOriginPrivateKey(pri,pub,chainId){var privateKey=new Uint8Array(pri);var publicKey=pub;if(publicKey[0]===3){privateKey=ecc.privateNegate(privateKey);}if(!privateKey){throw new Error('Private key is required for tweaking signer!');}var tweakedPrivateKey=ecc.privateAdd(privateKey,tapTweakHash(publicKey.slice(1),undefined));return new PrivateKey(bytesToHex(tweakedPrivateKey),chainId);}