import{Address}from'@onekeyfe/kaspa-core-lib';export var EKaspaAddressType=function(EKaspaAddressType){EKaspaAddressType["PayToPublicKey"]="pubkey";EKaspaAddressType["PayToScriptHash"]="scripthash";return EKaspaAddressType;}({});export function addressFromPublicKeyBuffer(pubKey,chainId){return Address.fromPublicKeyBuffer(pubKey,chainId).toString();}export function addressFromPublicKey(pubKey,chainId){return Address.fromPublicKey(pubKey,chainId).toString();}export function fromString(address,chainId,addressType=EKaspaAddressType.PayToPublicKey){return Address.fromString(address,chainId,addressType);}export function isValidAddress(address,chainId,addressType=EKaspaAddressType.PayToPublicKey){return Address.isValid(address,chainId,addressType);}